<template>
  <div class="positions-list">
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th>.</th>
      </tr>
      <tr
        class="squad-positions__item"
        v-for="player in getPlayers"
        :key="player.id"
      >
      <td>
        <CellInput :value="player.name.replace('_', ' ')" @edit="updateName(player.id, ...arguments)" />
      </td>
<!--         <td>
          <template>
            {{ player.name.replace('_', ' ') }}
          </template>
        </td> -->
        <td>{{ getRank(player.rankId).name }}</td>
        <td :style="{ 'background-color': getClist(getPosition(player.positionId).clist) + '66' }">{{ getPosition(player.positionId).name }}</td>
        <td class="edit-buttons">
          <div class="edit-buttons__item">
            <icon name="pen" class="icon" scale="1"></icon>
          </div>
          <div class="edit-buttons__item" @click="uninvite(player.id)">
            <icon name="times" class="icon icon_red" scale="1"></icon>
          </div>
        </td>
      </tr>
    </table>
    <UIButton title="" @click="addMember"/>
  </div>
</template>

<script>
import Icon from "vue-awesome/components/Icon";
import "vue-awesome/icons";
import UIButton from '../ui/UIButton.vue';
import CellInput from '../CellInput.vue';
export default {
  components: {
    Icon,
    UIButton,
    CellInput
  },
  
  computed: {
    getPositions() {
      return this.$store.getters["positions"].sort(
        (a, b) => b.priority - a.priority
      );
    },
    getPlayers() {
      return this.$store.getters["players"].filter(val => val.factionId === 1).sort((a, b) => b.rankId - a.rankId)
    }
  },
  methods: {
    getClist(id) {
      return this.$store.getters["getClistColor"](id);
    },
    getPosition(id) {
      return this.$store.getters["positionById"](id);
    },
    getSquad(id) {
        return this.$store.getters["squadById"](id)
    },
    getRank(id) {
        return this.$store.getters["getRank"](id)
    },
    uninvite(id) {
      this.$store.commit('updatePlayers', [id, {factionId: 0}])
    },
    addMember() {
      this.$store.commit('addPlayers', [{factionId: 1}])
    },
    updateName(id, name) {
      this.$store.commit('updatePlayers', [id, {name: name.replace(' ', '_')}])
    }
  }
};
</script>

<style lang="scss">
  .edit-buttons {
    display: flex;
    align-items: center;
    gap: 10px;
    &__item {
      cursor: pointer;
      .icon {
        fill: rgba(255, 255, 255, 0.9);
        &_red {
          fill: rgba(255, 0, 0, 0.9);
        }
      }
    }
  }
</style>