<template>
  <label class="field" :class="labelClassList">
    <input
      :type="type"
      class="field__input"
      :name="name"
      :placeholder="placeholder"
      v-model="newValue"
      :required="required"
      @input="$emit('input', $event.target.value)"
    />
    <span class="field__title" v-if="title">{{ title }}</span>
  </label>
</template>

<script>
export default {
  data() {
    return {
      newValue: ""
    };
  },

  mounted() {
    this.newValue = this.value;
  },

  watch: {
    value(newValue) {
      this.newValue = newValue;
    }
  },

  model: {
    prop: "value",
    event: "input"
  },

  props: {
    required: {
      type: Boolean,
      required: false,
      default: false
    },
    title: {
      type: String,
      required: false,
      default: ""
    },
    value: {
      type: String,
      required: false,
      default: ""
    },
    placeholder: {
      type: String,
      required: false,
      default: ""
    },
    name: {
      type: String,
      required: false,
      default: ""
    },
    labelClassList: {
      type: String,
      required: false,
      default: ""
    },
    errors: {
      type: Array,
      required: false,
      default: () => []
    },
    type: {
      type: String,
      required: false,
      default: "text"
    }
  }
};
</script>

<style lang="scss">
    .field {
        margin-bottom: 10px;

        &__input {
            width: 100%;
            margin-bottom: 10px;
            resize: none;
            background: rgba(255, 255, 255, 0.18);
            border: 2px solid rgb(187, 187, 187);
            color: white;
            //font-family: "Raleway";
            font-weight: 500;
            font-size: 15px;
            padding: 10px;
            border-radius: 8px;
            &:focus {
            outline: none !important;
            border: 2px solid rgba(255, 255, 255, 0.18);
            }
            &::placeholder {
                color: rgba(0, 0, 0, 0.3);
            }
        }
    }
</style>