<template>
  <div class="frame-upload">
    <div class="modal-top">
      <h2 class="modal__title"> </h2>
      <button class="modal__close" @click="hideModal"></button>
    </div>
    <div class="squad-edit">
        <div class="squad-edit__title">{{ squadData.name }}</div>
        <div class="squad-edit__clist">{{ squadData.clist }}</div>
        <!-- : {{ squadData.hortator !== null && getPlayer(squadData.hortator) ? getPlayer(squadData.hortator).replace("_", " ") : "-" }} -->
        <div class="squad-positions">
             :
            <table>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr class="squad-positions__item" v-for="position in squadPositions" :key="position.id">
                    <td>{{ position.name }}</td>
                    <td>{{ position.radioTag }}</td>
                    <td :style="{'background-color': getClist(position.clist) + '66'}">{{ position.clist }}</td>
                    <td>{{ position.alias ? '' : '' }}</td>
                    <td>{{ position.priority }}</td>
                </tr>
            </table>
        </div>
        <div class="squad-players">
             :
            <table>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr class="squad-players__item" v-for="player in squadPlayersSorted" :key="player.id">
                    <td :style="{'background-color': getClist(getPosition(player.positionId).clist) + '66'}">{{ player.name.replace('_', ' ') }}</td>
                    <td :style="{'background-color': getClist(getPosition(player.positionId).clist) + '66'}">{{ getPosition(player.positionId).name }}</td>
                    <td>{{ player.alias }}</td>
                    <td>{{ getRank(player.rankId).name }}</td>
                </tr>
            </table>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      squadData: {},
      squadPositions: [],
      squadPlayers: []
    };
  },
  mounted() {
    this.fetchSquadData();
    this.fetchSquadPositions();
    this.fetchSquadPlayers();
  },
  computed: {
    squadId() {
      return this.$store.getters["getSelectedSquad"];
    },
    squadPlayersSorted() {
        return this.squadPlayers.sort((a, b) => this.getPosition(b.positionId).priority - this.getPosition(a.positionId).priority)
    }
  },
  methods: {
    hideModal() {
      this.$store.commit("setPrimaryModal", "");
    },
    fetchSquadData() {
      this.squadData = JSON.parse(
        JSON.stringify(this.$store.getters["squadById"](this.squadId))
      );
    },
    fetchSquadPositions() {
      this.squadPositions = JSON.parse(
        JSON.stringify(this.$store.getters["squadPositions"](this.squadId))
      );
    },
    fetchSquadPlayers() {
      this.squadPlayers = JSON.parse(
        JSON.stringify(this.$store.getters["squadPlayers"](this.squadId))
      );
    },
    getRank(id) {
        return this.$store.getters["getRank"](id)
    },
    getPosition(id) {
        return this.$store.getters["getPosition"](id)
    },
    getPlayer(id) {
        return this.$store.getters["getPlayer"](id)
    },
    getClist(id) {
        return this.$store.getters["getClistColor"](id)
    }
  }
};
</script>

<style lang="scss">
.modal-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
</style>