<template>
  <button v-on="$listeners" :class="`${active ? 'active ' : ''}${classList}`">
    {{ title }}
  </button>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            required: false,
            default: "click"
        },
        active: {
            type: Boolean,
            required: false,
            default: false,
        },
        classList: {
            type: String,
            required: false,
            default: "",
        }
    }
}
</script>

