        background-color: white;
        color: black;
        border-radius: 8px;
        padding: 8px;
        transition: all .2s ease-in-out;
        &:hover {
            //opacity: 0.8;
        }
        &:active {
            //opacity: 0.6;
        }
        &.active {
            background-color: black;
            color: white;
        }
    }
