<template>
  <div class="frame">
    <div class="squads">
      <div
        class="squad"
        :style="{
          'background-color': getClistColor(squad.clist) + '77',
          border: `2px solid ${getClistColor(squad.clist) + 'BB'}`
        }"
        v-for="squad in getSquadData"
        :key="squad.id"
        @click="editSquad(squad.id)"
      >
        <div class="squad-top">
          <span class="squad__name">{{ squad.name }}</span>
          <span class="squad__amount"
            ><icon name="user" class="icon" scale="1"></icon
            >{{ getSquadCapacity(squad.id) }}</span
          >
        </div>
        <div class="squad-positions">
          <div
            class="squad-positions__item"
            v-for="position in squadPositions(squad.id)"
            :key="position.id"
          >
            - {{ position.name }}
          </div>
        </div>
      </div>
    </div>
    <div class="controls">
      <UIButton title="" @click="addSquad" />
    </div>
  </div>
</template>

<script>
import Icon from "vue-awesome/components/Icon";
import "vue-awesome/icons";
import UIButton from "../ui/UIButton.vue";
export default {
  components: { Icon, UIButton },
  computed: {
    getSquadData() {
      return this.$store.getters["squads"];
    }
  },
  methods: {
    squadPositions(id) {
      return this.$store.getters["squadPositions"](id);
    },
    getClistColor(id) {
      return this.$store.getters["getClistColor"](id);
    },
    getSquadCapacity(id) {
      return this.$store.getters["squadPlayers"](id).length;
    },

    editSquad(id) {
        this.$store.commit("setSelectedSquad", id)
      this.$store.commit("setPrimaryModal", "Squad");
    },
    addSquad() {}
  }
};
</script>

<style lang="scss">
.frame {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.squads {
  display: grid;
  gap: 20px;
  grid-auto-rows: minmax(150px, auto);
  grid-template-columns: repeat(auto-fill, 300px);
}
.squad {
  padding: 15px;
  width: 300px;
  height: 100%;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease-in;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
  &-top {
    display: flex;
    justify-content: space-between;
  }
  &__name {
    font-size: 30px;
  }
  &__amount {
    display: flex;
    gap: 5px;
    align-items: center;
    .icon {
      fill: white;
      color: white;
    }
  }
}
</style>