<template>
  <div class="positions-list">
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>

        <th></th>
        <th>. </th>
        <th>. </th>
        <th>. </th>
        <th> </th>
      </tr>
      <tr
        class="squad-positions__item"
        v-for="position in getPositions"
        :key="position.id"
      >
        <td>{{ position.name }}</td>
        <td>{{ position.radioTag }}</td>
        <td :style="{ 'background-color': getClist(position.clist) + '66' }">
          {{ position.clist }}
        </td>
        <td>{{ position.alias ? "" : "" }}</td>
        <td>{{ position.priority }}</td>

        <td :style="{ 'background-color': getClist(getSquad(position.squadId).clist) + '66' }">{{ getSquad(position.squadId).name }}</td>
        <td>{{ getRank(position.minRank).name }}</td>
        <td>{{ getRank(position.maxRank).name }}</td>
        <td>{{ position.limit === null ? '' : position.limit }}</td>
        <td>{{ playersByPosition(position.id) }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  computed: {
    getPositions() {
      return this.$store.getters["positions"].sort(
        (a, b) => b.priority - a.priority
      );
    }
  },
  methods: {
    getClist(id) {
      return this.$store.getters["getClistColor"](id);
    },
    getSquad(id) {
        return this.$store.getters["squadById"](id)
    },
    getRank(id) {
        return this.$store.getters["getRank"](id)
    },
    playersByPosition(id) {
        return this.$store.getters["playersByPosition"](id).length
    }
  }
};
</script>

