<template>
  <div>
    <div class="squad-image" ref="image">
        <div class="watermark">@Teodoro_Bagwell</div>
      <div class="squad-image-left">
        <div class="squad-image__rank">
          {{ editorData.rank || "." }}
        </div>
        <div class="squad-image__name">
          {{ editorData.name || "Teodoro Bagwell" }}
        </div>
      </div>
      <div
        class="squad-image-circle"
        :style="{
          'border-color': getColorByClist,
          'background-image': `url(${editorData.image})`,
          'border-width': editorData.size == 2 ? '15px' : '12px',
          'width': editorData.size == 2 ? '200px' : '160px',
          'height': editorData.size == 2 ? '200px' : '160px',
          'min-width': editorData.size == 2 ? '200px' : '160px',
        }"
      >
        <!-- <img :src="editorData.image.image"> -->
      </div>
      <div class="squad-image-right">
        <div class="squad-image__position">
          {{ editorData.position || "Dep.Head" }}
        </div>
        <div class="squad-image__squad" :style="{ color: getColorByClist }">
          {{ editorData.squad || "SID" }}
        </div>
      </div>
    </div>
    <!-- <UIButton title="" @click="downloadImage"/> -->

  </div>
</template>

<script>
import UIButton from '../ui/UIButton.vue';
import html2canvas from "html2canvas"
export default {
  components: { UIButton },
  props: {
    editorData: {
      type: Object,
      required: true,
      default: () => ({})
    }
  },
  data() {
    return {
      clistData: {},
      downloadUrl: "",
    };
  },
  methods: {
    async downloadImage() {
        const el = this.$refs.image
        const options = {
            type: "dataURL",
            backgroundColor: null,
        }
        const printCanvas = await html2canvas(el, options);

        const link = document.createElement("a");
        link.setAttribute("download", "download.png");
        link.setAttribute(
            "href",
            printCanvas
            .toDataURL("image/png")
            .replace("image/png", "image/octet-stream")
        );
        link.click();

    }
  },
  computed: {
    getColorByClist() {
      return this.$store.getters["getClistColor"](this.editorData.clist)
    }
  }
};
</script>

<style lang="scss">
.squad-image {
  //font-family: "Dela";
  position: relative;
  display: flex;
  font-size: 30px;
  color: white;
  justify-content: center;
  align-items: center;
  gap: 10px;
  &-left {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    font-size: 20px;
    min-width: 250px;
    width: 250px;
    text-align: right;
  }
  &-circle {
    width: 200px;
    min-width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 15px solid rgb(168, 21, 21);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    box-shadow: 5px 5px 8px 2px rgba(0, 0, 0, 0.25);
  }
  &-right {
    font-size: 60px;
    line-height: 60px;
    min-width: 350px;
    width: 350px;
  }
  &__rank {
    font-size: 20px;
  }
  &__position {
    font-size: 40px;
    line-height: 40px;
  }
  &__squad {
  }
  .watermark {
    font-size: 12px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0.03;
  }
}
</style>