<template>
  <div>
    <div
      class="members-image"
      ref="image"
      :class="{ 'members-image_leader': player.isLeader }"
      v-if="!vacant"
    >
      <div
        class="members-image-circle"
        :style="{ 'background-image': `url(${player.avatar})` }"
      ></div>
      <div class="members-image__position">
        <div class="ribbon">
          <div class="ribbon-edge ribbon-left"></div>
          <div class="ribbon-center"></div>
          <div class="ribbon-edge ribbon-right"></div>
        </div>
        <span class="position-name">{{
          getPosition(player.positionId).name
        }}</span>
      </div>
      <div class="members-image__name">
        {{ player.name.replace("_", " ") }}
      </div>
    </div>

    <div
      class="members-image members-image_vacant"
      ref="image"
      :class="{ 'members-image_leader': rank.isLeader }"
      v-else
    >
      <div
        class="members-image-circle"
      ></div>
      <div class="members-image__position">
        <div class="ribbon">
          <div class="ribbon-edge ribbon-left"></div>
          <div class="ribbon-center"></div>
          <div class="ribbon-edge ribbon-right"></div>
        </div>
        <span class="position-name">-</span>
      </div>
      <div class="members-image__name">
        
      </div>
    </div>
  </div>
</template>

<script>
import UIButton from "../ui/UIButton.vue";
export default {
  components: { UIButton },
  props: {
    player: {
      type: Object,
      required: false,
      default: () => ({})
    },
    vacant: {
      type: Boolean,
      required: false,
      default: false
    },
    rank: {
      type: Object,
      required: false,
      default: () => ({})
    }
  },
  data() {
    return {
      clistData: {},
      downloadUrl: ""
    };
  },
  methods: {
    getRank(id) {
      return rank ? rank.id : this.$store.getters["getRank"](id);
    },
    getPosition(id) {
      return this.$store.getters["getPosition"](id);
    }
  },
  computed: {
    getColorByClist() {
      return this.$store.getters["getClistColor"](this.player.position);
    }
  }
};
</script>

<style lang="scss">
.members-image {
  position: relative;
  height: fit-content;
  width: fit-content;
  min-height: 260px;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  text-align: center;
  white-space: nowrap;
  min-width: 450px;
  color: white;
  justify-content: center;
  align-items: center;
  gap: 10px;
  &_vacant {
    opacity: 0.2;
  }
  &_leader {
    .members-image-circle {
      border-width: 15px;
      width: 200px;
      height: 200px;
      min-width: 200px;
    }
    .members-image__name {
      font-size: 40px;
    }
  }
  &-left {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    font-size: 20px;
    min-width: 250px;
    width: 250px;
    text-align: right;
  }
  &-circle {
    position: relative;
    width: 200px;
    min-width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 15px solid #3861ad;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    //box-shadow: 5px 5px 8px 2px rgba(0, 0, 0, 0.25);
    border-width: 12px;
    width: 160px;
    height: 160px;
    min-width: 160px;
  }
  &-right {
  }
  &__rank {
    font-size: 20px;
  }
  &__name {
    position: relative;
    bottom: 40px;
    z-index: 5;
    font-size: 32px;
    font-weight: 600;
    line-height: 40px;
    color: black;
  }
  &__position {
    position: relative;
    bottom: 30px;
    padding: 3px 40px;
    .ribbon {
      left: 50%;
      transform: translateX(-50%) perspective(120px) rotateX(-45deg);
      position: absolute;
      &-edge {
        position: absolute;
        top: 0;
        width: 70px;
        height: 30px;
        background: rgb(187, 0, 0);
      }
      &-left {
        transform: translateY(-30%);
        left: -50px;
      }
      &-right {
        transform: translateY(-30%);
        right: -50px;
      }
      &-center {
        position: relative;
        background: red;
        z-index: 5;
        height: 30px;
        width: 270px;
      }
    }
    .position-name {
      position: relative;
      font-size: 17px;
      z-index: 10;
    }
  }
  &__squad {
  }
  .watermark {
    font-size: 12px;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
      "Lucida Sans", Arial, sans-serif;
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0.03;
  }
}
</style>