<template>
  <div @focus="focus()" tabindex="-1">
    <div v-if="!edit" @focus="edit = true; focus()" class="cell-input-static">
      {{ value }}
    </div>
    <form @submit.prevent="edit = false; $emit('edit', input)" v-show="edit">
        <input ref="cell-input" type="text" v-model="input" class="cell-input"  @focusout="edit = false">
    </form>
</div>
</template>

<script>
export default {
    data() {
        return {
            edit: false,
            input: "",
        }
    },
    methods: {
        async focus() {
            this.edit = true;
            await new Promise((resolve) => setTimeout(resolve, 100));
            this.$refs['cell-input'].focus()
        }
    },
    mounted() {
        this.input = this.value
    },
    props: {
        value: {
            type: String,
            default: "",
            required: false,
        }
    }
};
</script>

<style lang="scss">
    .cell-input {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        padding: 5px 8px;
        font-family: 'Rubik';
        font-size: inherit;
        &:focus {
            outline: none;
        }
    }
</style>