<template>
  <div class="frame">
    <div class="ranks">
      <div class="rank" v-for="rank in ranks.filter(val => val.id > 10)" :key="rank.id">
        <div class="copyright" v-if="rank.id === 11"><i>: Teodoro Bagwell</i></div>
        <div class="rank-shadow rank-shadow-top"></div>
        <div class="rank-shadow rank-shadow-bottom"></div>
        <div v-if="rank.id > 10">
          <div class="rank-title">{{ rank.nameMultiple }}</div>
          <div class="rank-players">
            <div
              class="rank-player"
              v-for="player in players.filter((val) => val.rankId === rank.id)"
              :key="player.id"
            >
              <PlayerImage :player="player" />
            </div>
            <template v-if="rank.limit && players.filter((val) => val.rankId === rank.id).length < rank.limit">
            <div
              class="rank-player"
              v-for="rank, index in Array(rank.limit - players.filter((val) => val.rankId === rank.id).length).keys()"
              :key="index + rank.id"
            >
              <PlayerImage vacant :rank="rank" />
            </div>
          </template>
          </div>
        </div>
        <div v-else>
          <div class="rank-title">{{ rank.nameMultiple }}</div>
          <div class="rank-players">
            <div
              class="rank-player"
              v-for="player in players.filter((val) => val.rankId === rank.id)"
              :key="player.id"
            >
              - {{ player.name }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Icon from "vue-awesome/components/Icon";
import "vue-awesome/icons";
import UIButton from "../ui/UIButton.vue";
import PlayerImage from "./PlayerImage.vue";
export default {
  components: { Icon, UIButton, PlayerImage },
  computed: {
    players() {
      return this.$store.getters["players"];
    },
    ranks() {
      return JSON.parse(JSON.stringify(this.$store.getters["policeRanks"]))
        .reverse()
        .filter((val) => val.name !== "-");
    }
  },
  methods: {
    squadPositions(id) {
      return this.$store.getters["squadPositions"](id);
    },
    getClistColor(id) {
      return this.$store.getters["getClistColor"](id);
    },
    getSquadCapacity(id) {
      return this.$store.getters["squadPlayers"](id).length;
    },

    editSquad(id) {
      this.$store.commit("setSelectedSquad", id);
      this.$store.commit("setPrimaryModal", "Squad");
    },
    addSquad() {}
  }
};
</script>

