<template>
  <div class="editor-header">
    <UIButton :title="tab.title" v-for="tab in tabData" :key="tab.key" @click="setActiveTab(tab.key)" :active="getActiveTab === tab.key" classList="editor-header__button"/>
  </div>
</template>

<script>
import UIButton from './ui/UIButton.vue'
export default {
  components: { UIButton },
  data() {
    return {
        tabData: [
        {
                title: ": ",
                key: "leader_members"
            },
            {
                title: ": ",
                key: "squads"
            },
            {
                title: ": ",
                key: "positions_faction"
            },
            {
                title: ":  ",
                key: "faction"
            },
            {
                title: ":  ",
                key: "members"
            },
            {
                title: ":  ",
                key: "positions"
            },
        ]
    }
  },
    methods: {
        setActiveTab(tab) {
            this.$store.commit("setActiveTab", tab)
        }
    },
    computed: {
        getActiveTab() {
            return this.$store.getters["getActiveTab"]
        }
    }
}
</script>

<style lang="scss">
    .editor-header {
        //padding-bottom: 20px;
        display: flex;
        &__button {
            background-color: rgba(50, 70, 84, 0.5);
            color: rgba(255, 255, 255, 0.5);
            padding: 15px;
            border-radius: 0;
            border-left: 1px solid rgba(50, 70, 84, 0.5);
            overflow: hidden;
            &:first-child {
                border-left: 0px;
                border-radius: 14px 0 0 0;
            }
            &:last-child {
                border-radius: 0 14px 0 0;
            }
            &.active {
                background-color: rgb(50, 70, 84);
                color: white;
            }
        }
    }
</style>